import React, { useState } from 'react';
import { Form, Input, Button, Card, message } from 'antd';
import { register } from '../api';
import { useNavigate } from 'react-router-dom';

const Register = () => {
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const onFinish = async (values) => {
    setLoading(true);
    try {
      await register(values);
      message.success('');
      navigate('/login');
    } catch (err) {
      message.error(err.response?.data?.message || '');
    }
    setLoading(false);
  };

  return (
    <div style={{ minHeight: '100vh', background: '#e6f7ff', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
      <Card style={{ width: 350, borderRadius: 16 }}>
        <h2 style={{ color: '#1890ff', textAlign: 'center' }}></h2>
        <Form layout="vertical" onFinish={onFinish}>
          <Form.Item name="username" label="" rules={[{ required: true, message: '' }]}>
            <Input />
          </Form.Item>
          <Form.Item name="password" label="" rules={[{ required: true, message: '' }]}>
            <Input.Password />
          </Form.Item>
          <Form.Item name="name" label="" rules={[{ required: true, message: '' }]}>
            <Input />
          </Form.Item>
          <Form.Item name="email" label="">
            <Input />
          </Form.Item>
          <Form.Item name="phone" label="">
            <Input />
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit" block loading={loading} style={{ borderRadius: 8 }}>
              
            </Button>
          </Form.Item>
          <Form.Item>
            <Button type="link" block onClick={() => navigate('/login')}>
              
            </Button>
          </Form.Item>
        </Form>
      </Card>
    </div>
  );
};

export default Register;